import { Injectable } from '@angular/core';
import * as data from "../assets/osrs_quests.json"
import * as xpdata from "../assets/skill_quest_xp.json"

@Injectable({
  providedIn: 'root'
})
export class UtilsService {

  quests;
  skillQuestXp;
  player = {};

  savePlayer(){
    localStorage["player"]=JSON.stringify(this.player);
  }

  constructor() { 
    this.quests = data["default"];
    this.skillQuestXp = xpdata["default"].filter(s=>s.xp!="Experience\nreward");
    if(localStorage["player"])
      this.player = JSON.parse(localStorage["player"])
  }

  getCompleted(){
    if(!localStorage["completed"])
      localStorage["completed"] = JSON.stringify([]);
    return JSON.parse(localStorage["completed"]);
  }
  
  isQuestCompleted(quest){
    return this.getCompleted().includes(quest);
  }

  isCompletedIcon(quest){
    return this.isQuestCompleted(quest) ? "lens" : "panorama_fish_eye"
  }

  toggleCompleted(quest){
    let completed =this.getCompleted();
    let index = completed.findIndex(q=>q==quest);
    if(index==-1)
      completed.push(quest)
    else{
      completed.splice(index,1);
      if(this.isQuestTodo(quest))
        this.toggleTodo(quest)
    }
    localStorage["completed"]=JSON.stringify(completed);
  }

  getTodo(){
    if(!localStorage["todo"])
    localStorage["todo"] = JSON.stringify([]);
    return JSON.parse(localStorage["todo"]);
  }
  
  isQuestTodo(quest){
    return this.getTodo().includes(quest);
  }

  isTodoIcon(quest){
    return this.getTodo().includes(quest) ? "star" : "star_border"
  }

  toggleTodo(quest){
    let todo =this.getTodo();
    let index = todo.findIndex(q=>q==quest);
    if(index==-1)
      todo.push(quest)
    else
      todo.splice(index,1);
    localStorage["todo"]=JSON.stringify(todo);
  }

  getQuest(quest){
    let t = this.quests.find(q=>q.name==quest);
    if(!t)
      return {name:quest, questRequirements:[], skillRequirements:[]};
    return t;
  }

  fixQuestUrl(quest){
    return quest.replace(/[ ]/g,"_");
  }

  getQuestRequirements(quest){
    var todo = [];
    todo.push({name:quest.name, indent:-1});
    var index = 0;
    while(todo[index]){
      todo.splice(index+1,0,...(this.getQuest(todo[index].name).questRequirements.map(q=>{
        return {name:q,indent:todo[index].indent+1}
      })));
      index++;
    }
    todo.splice(0,1);
    return todo;
  }
  
  isQuestDoable(quest){
    let q = this.getQuest(quest);
    return this.getQuestRequirements(q).filter(e=>!this.isQuestCompleted(e.name)).length == 0 &&
      (q.skillRequirements.length==0 || q.skillRequirements.filter(e=>!this.isSkillAchieved(e)).length == 0)
  }

//SKILLS

  skills = ["Attack", "Strength", "Defence", "Ranged", "Prayer", "Magic", "Runecraft", "Hitpoints", "Crafting", "Mining", "Smithing", "Fishing", "Cooking", "Firemaking", "Woodcutting", "Agility", "Herblore", "Thieving", "Fletching", "Slayer", "Farming", "Construction", "Hunter"];
  skillImages = ["https://oldschool.runescape.wiki/images/thumb/f/fe/Attack_icon.png/21px-Attack_icon.png?b4bce", "https://oldschool.runescape.wiki/images/1/1b/Strength_icon.png?e6e0c", "https://oldschool.runescape.wiki/images/b/b7/Defence_icon.png?ca0cd", "https://oldschool.runescape.wiki/images/thumb/1/19/Ranged_icon.png/21px-Ranged_icon.png?01b0e", "https://oldschool.runescape.wiki/images/thumb/f/f2/Prayer_icon.png/21px-Prayer_icon.png?ca0dc", "https://oldschool.runescape.wiki/images/thumb/5/5c/Magic_icon.png/21px-Magic_icon.png?334cf", "https://oldschool.runescape.wiki/images/thumb/6/63/Runecraft_icon.png/21px-Runecraft_icon.png?c278c", "https://oldschool.runescape.wiki/images/thumb/9/96/Hitpoints_icon.png/21px-Hitpoints_icon.png?a4819", "https://oldschool.runescape.wiki/images/thumb/c/cf/Crafting_icon.png/21px-Crafting_icon.png?a1f71", "https://oldschool.runescape.wiki/images/thumb/4/4a/Mining_icon.png/21px-Mining_icon.png?00870", "https://oldschool.runescape.wiki/images/d/dd/Smithing_icon.png?d26c5", "https://oldschool.runescape.wiki/images/thumb/3/3b/Fishing_icon.png/21px-Fishing_icon.png?15a98", "https://oldschool.runescape.wiki/images/d/dc/Cooking_icon.png?a0156", "https://oldschool.runescape.wiki/images/thumb/9/9b…iremaking_icon.png/21px-Firemaking_icon.png?45ea0", "https://oldschool.runescape.wiki/images/thumb/f/f4…dcutting_icon.png/17px-Woodcutting_icon.png?6ead4", "https://oldschool.runescape.wiki/images/thumb/8/86/Agility_icon.png/21px-Agility_icon.png?389e0", "https://oldschool.runescape.wiki/images/thumb/0/03/Herblore_icon.png/21px-Herblore_icon.png?ffa9e", "https://oldschool.runescape.wiki/images/thumb/4/4a/Thieving_icon.png/21px-Thieving_icon.png?973fe", "https://oldschool.runescape.wiki/images/thumb/9/93/Fletching_icon.png/21px-Fletching_icon.png?15cda", "https://oldschool.runescape.wiki/images/thumb/2/28/Slayer_icon.png/20px-Slayer_icon.png?cd34f", "https://oldschool.runescape.wiki/images/thumb/f/fc/Farming_icon.png/21px-Farming_icon.png?558fa", "https://oldschool.runescape.wiki/images/thumb/f/f6…ruction_icon.png/21px-Construction_icon.png?f9bf7", "https://oldschool.runescape.wiki/images/thumb/d/dd/Hunter_icon.png/20px-Hunter_icon.png?8762f"];
  


  isSkillAchieved(skill){
    return this.player[skill.skill] >= skill.level;
  }

  isSkillAchievedIcon(skill){
    return this.isSkillAchieved(skill) ? "lens" : "panorama_fish_eye"
  }

  getSkillImage(skill){
    return this.skillImages[this.skills.findIndex(s=>s==skill.skill)];
  }

  getSkillReqInfo(skill){
    return skill.skill+": "+skill.level+" "+skill.extra;
  }

  toggleSkill(skill){
    if(!this.isSkillAchieved(skill))
      this.player[skill.skill] = skill.level;
  }



}
