import { UtilsService } from './../utils.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-quests',
  templateUrl: './quests.component.html',
  styleUrls: ['./quests.component.scss']
})
export class QuestsComponent implements OnInit {

  name: string;
  showOnlyAvailable;

  constructor(public utils: UtilsService) { }

  ngOnInit() {
  }

  filter(quests){
    return quests.filter(q=>(!this.name || q.name.toLowerCase().includes(this.name.toLowerCase()))
      && (!this.showOnlyAvailable || this.utils.isQuestDoable(q.name))
    ).sort((a,b)=>a.name.replace(/^The[ ]/g,"") < b.name.replace(/^The[ ]/g,"") ? -1 : a.name.replace(/^The[ ]/g,"") > b.name.replace(/^The[ ]/g,""))
  }

}
