import { UtilsService } from './../utils.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  upcoming;

  constructor(public utils:UtilsService) { }

  ngOnInit() {
  }
  
  todoQuests(){
    var todo = [];
    this.utils.getTodo().forEach(q=>todo.push({name:q, indent:0}));
    if(todo.length == 0)
      return [];
    var index = 0;
    while(todo[index]){
      todo.splice(index+1,0,...(this.utils.getQuest(todo[index].name).questRequirements.filter(q=>!this.utils.isQuestCompleted(q)).map(e=>{return{name:e,indent:todo[index].indent+1}})));
      index++;
    }

    return todo;
  }

  todoSkillRequirements(){
    let skills = [];
    this.todoQuests().forEach(q=>skills=skills.concat(this.utils.getQuest(q.name).skillRequirements.filter(s=>!this.utils.isSkillAchieved(s))))
    return skills.sort((a,b)=>
      this.utils.skills.findIndex(s=>s==a.skill) < this.utils.skills.findIndex(s=>s==b.skill) ? -1 :
        this.utils.skills.findIndex(s=>s==a.skill) > this.utils.skills.findIndex(s=>s==b.skill) ? 1 : a.level-b.level
    )
  }

  getUpcoming(){
    let diffs = ["Novice", "Intermediate", "Special", "Experienced", "Master"];
    let lengths = ["Very short", "Short", "Short – Medium", "Medium", "Medium – Long", "Long", "Long – Very long", "Very long"];
    if(!this.upcoming){
      let quests = this.todoQuests().filter(q=>this.utils.isQuestDoable(q.name)).sort((a,b)=>{
        let ind = b.indent-a.indent;
        let diff = diffs.findIndex(d=>d==this.utils.getQuest(a.name).diff)-diffs.findIndex(d=>d==this.utils.getQuest(b.name).diff);
        let length = lengths.findIndex(l=>l==this.utils.getQuest(a.name).length)-lengths.findIndex(l=>l==this.utils.getQuest(b.name).length);

        return length != 0 ? length : diff != 0 ? diff : ind;
      });
      if(!quests || quests.length == 0)
        return null;
      return quests[0];
    }
    return this.upcoming;
  }

  hasUpcoming(){
    return this.getUpcoming()!=null;
  }

}
